.countdown {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--701);
  font-family: "Poppins", sans-serif;
  overflow: hidden;
}
.countdown #time {
  display: flex;
  gap: 30px;
  transform: scale(75%);
}
.countdown #time .circle {
  position: relative;
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.countdown #time .circle svg {
  position: relative;
  width: 150px;
  height: 150px;
  transform: rotate(270deg);
}
.countdown #time .circle svg circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  stroke-width: 8;
  stroke: #282828;
  stroke-linecap: round;
  transform: translate(5px, 5px);
}
.countdown #time .circle svg circle:nth-child(2) {
  stroke: var(--clr);
  stroke-dasharray: 440;
  stroke-dashoffset: 440;
}
.countdown #time div {
  position: absolute;
  text-align: center;
  font-weight: 500;
  color: #fff;
  font-size: 1.5em;
}
.countdown #time div span {
  color: hsla(0, 0%, 100%, 0.747);
  position: absolute;
  transform: translateX(-50%) translateY(5px);
  font-size: 0.35em;
  font-weight: 300;
  letter-spacing: 0.1px;
  text-transform: uppercase;
}
.countdown #time .dots {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  z-index: 1000;
}
.countdown #time .dots:before {
  content: "";
  position: absolute;
  top: -3px;
  width: 15px;
  height: 15px;
  background: var(--clr);
  border-radius: 50%;
  box-shadow: 0 0 20px var(--clr), 0 0 60px var(--clr);
}

.home_body {
  min-height: 100vh;
}
.home_body input,
.home_body h1,
.home_body h2,
.home_body a,
.home_body p,
.home_body button {
  color: rgb(10, 10, 10);
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
}
.home_body #app {
  background-color: rgb(20, 20, 20);
  position: relative;
  height: 300vh;
}
.home_body #app #calendar {
  background: hsla(0, 0%, 0%, 0.459);
  position: relative;
  width: 100vw;
}
.home_body #app #calendar #calendar-dates {
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 20px;
  box-shadow: rgba(40, 11, 207, 0.15) 0 48px 100px 0;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  height: 235vh;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, 10%);
  width: 1050px;
}
.home_body #app #calendar #calendar-dates .date-wrapper {
  background-color: transparent;
  border: none;
  box-shadow: inset 0 0 6px 2px rgba(0, 0, 0, 0.03), inset 0 0 10px 8px rgba(0, 0, 0, 0.02);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  gap: 10px;
  height: 140px;
  outline: none;
  padding: 10px;
  transition: transform 250ms;
  width: 140px;
  border-radius: 26px;
}
.home_body #app #calendar #calendar-dates .date-wrapper:hover, .home_body #app #calendar #calendar-dates .date-wrapper:focus {
  cursor: pointer;
  transform: translateY(-5px);
}
.home_body #app #calendar #calendar-dates .date-wrapper:hover .date, .home_body #app #calendar #calendar-dates .date-wrapper:focus .date {
  background-color: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.25);
}
.home_body #app #calendar #calendar-dates .date-wrapper .date {
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 48px 100px 0 rgba(17, 12, 46, 0.02);
  flex-grow: 1;
  justify-content: space-between;
  position: relative;
  text-align: left;
  transition: background-color 250ms;
  width: calc(100% - 2px);
  border-radius: 20px;
}
.home_body #app #calendar #calendar-dates .date-wrapper .date .date-day {
  border-radius: 100px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: inset 0 0 8px 4px rgba(0, 0, 0, 0.04), inset 0 0 6px 4px rgba(0, 0, 0, 0.02);
  display: inline-flex;
  gap: 5px;
  margin: 10px;
  padding: 4px 12px;
}
.home_body #app #calendar #calendar-dates .date-wrapper .date .date-day h2 {
  text-align: center;
}
.home_body #app #calendar #calendar-dates .date-wrapper .date .date-day .date-day-of-month {
  color: white;
  font-size: 0.8em;
}
.home_body #app #calendar #calendar-dates .date-wrapper .date .date-day .date-day-of-week {
  font-weight: 700;
  text-transform: uppercase;
  font-size: 0.8em;
  color: rgba(146, 127, 253, 0.7058823529);
  margin-top: 2px;
}
.home_body #app #calendar #calendar-dates .date-wrapper .date .date-title {
  align-items: start;
  display: flex;
  gap: 8px;
  padding: 10px;
}
.home_body #app #calendar #calendar-dates .date-wrapper .date .date-title .date-title-dot {
  background-color: white;
  flex-shrink: 0;
  border-radius: 5px;
  height: 5px;
  margin-top: 5px;
  width: 5px;
}
.home_body #app #calendar #calendar-dates .date-wrapper .date .date-title h2 {
  color: white;
  word-break: break-word;
  font-size: 1em;
}
.home_body #app #calendar #calendar-dates .date-wrapper .active-day-indicator {
  border-radius: 5px;
  height: 5px;
  margin: 0 20px;
  width: calc(100% - 40px);
}

.days__body {
  font-family: "Roboto", sans-serif;
  background-color: var(--701);
  min-height: 100vh;
  padding-inline: 50px;
  padding-block: 56px;
}
.days__body main {
  padding-bottom: 200px;
}

.days__body header {
  position: relative;
}
.days__body header time {
  position: absolute;
  color: #fff;
  padding: 10px;
  pointer-events: none;
  animation: day-date 10s linear 5s infinite;
}
@keyframes day-date {
  0% {
    text-shadow: 0 0 0 rgba(255, 0, 0, 0);
  }
  20% {
    text-shadow: 0 0 5px #9900ff;
  }
  80% {
    text-shadow: 0 0 5px #9900ff;
  }
  100% {
    text-shadow: 0 0 0 rgba(255, 0, 0, 0);
  }
}
.days__body header iframe {
  width: 100%;
  overflow: hidden;
}

* {
  scrollbar-width: none;
}
*::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: hsla(0, 0%, 0%, 0);
}
*::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 0%, 0);
}

:root {
  --901: hsl(258, 80%, 56%);
  --801: hsl(258, 79%, 36%);
  --701: hsl(0, 0%, 2%);
  --600: hsl(0, 0%, 20%);
  --601: #595959;
  --501: #fafafa;
  --902: 333, 93%, 56%;
  --802: 309, 77%, 40%;
  --702: 276, 91%, 38%;
  --602: 268, 88%, 36%;
  --502: 263, 87%, 35%;
  --402: 258, 86%, 34%;
  --302: 243, 57%, 50%;
  --202: 229, 83%, 60%;
  --102: 212, 84%, 61%;
  --103: 194, 85%, 62%;
  --bg-body: hsl(0, 0%, 2%);
  --bg-body-light: hsl(0, 0%, 90%);
  --boxActive: hsla(283, 82%, 20%, 0.545);
  --boxActive-light: hsl(268, 100%, 32%);
  --boxBorder: hsla(0, 0%, 100%, 0.425);
  --boxHover: hsla(0, 0%, 100%, 0.06);
  --box-bg: hsl(0, 0%, 8%);
  --box-bg-light: hsl(0, 0%, 3%);
  --box-t: hsl(0, 0%, 60%);
  --card-title: hsl(0, 0%, 100%);
  --card-title-bg-lg: hsl(0, 0%, 0%);
  --card-bg-lg: hsla(0, 0%, 0%, 0.35);
  --dark-theme-btn: hsl(0, 0%, 100%);
  --dark-theme-btn-circle: hsl(0, 0%, 0%);
  --light-theme-btn: hsl(0, 0%, 0%);
  --light-theme-btn-circle: hsl(0, 0%, 100%);
}

.days__body .home-svg {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--701);
  height: 100%;
  width: 60px;
  margin-bottom: 5px;
}
.days__body .home-svg a {
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: hsla(0, 0%, 100%, 0.119);
  transition: border-radius 180ms ease-in-out;
  border-radius: 50%;
}
.days__body .home-svg a i {
  color: hsl(214, 4%, 65%);
  font-size: 1.5rem;
  transition: color 180ms ease-in-out;
}
.days__body .home-svg a:hover {
  border-radius: 30%;
}
.days__body .home-svg a:hover i {
  color: hsl(0, 0%, 100%);
}

.days__body main section {
  display: block;
  min-height: 300px;
}

.days__body main .statistics-section {
  padding-inline: 30px;
  gap: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.days__body main .statistics-section a img {
  height: 130px;
  border-radius: 5px;
}

.days__body main .projects-update {
  background-color: hsla(273, 100%, 50%, 0.025);
  display: grid;
  justify-content: center;
  position: relative;
  padding: 20px 0;
}

article {
  display: flex;
  flex-direction: column;
}
article .project-title {
  display: flex;
  justify-content: center;
}
article .project-title h3 {
  text-shadow: 0 0 1px;
  color: hsl(0, 0%, 100%);
}
article .repo-info {
  padding: 20px 0 0 0;
  flex-wrap: wrap;
  gap: 5px;
}
article .repo-new-features {
  padding: 15px 0 10px 12px;
  color: hsla(0, 0%, 100%, 0.685);
}
article .repo-new-features iframe {
  display: block;
  margin-inline: auto;
}
@media (max-width: 800px) {
  article .repo-new-features iframe {
    width: 600px;
    height: 340px;
  }
}
article .repo-new-features .commit-info a {
  padding-left: 7px;
  color: hsla(315, 100%, 50%, 0.322);
}
article .repo-new-features .commit-info a:hover {
  text-shadow: 0 0 5px;
}
article .repo-new-features h4 {
  color: hsla(0, 0%, 100%, 0.808);
  padding-block: 20px 7px;
}
article .repo-new-features ul {
  padding-left: 20px;
  list-style: none;
}
article .repo-new-features ul li {
  position: relative;
  padding-block: 3px;
}
article .repo-new-features ul li::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: solid 5px transparent;
  border-left-color: var(--901);
  top: 50%;
  transform: translateY(-50%);
  left: -10px;
}

.section-title-contanier {
  --purple: rgb(123, 31, 162);
  --violet: rgb(103, 58, 183);
  --pink: rgb(244, 143, 177);
}
@keyframes background-pan {
  from {
    background-position: 0% center;
  }
  to {
    background-position: -200% center;
  }
}
@keyframes scale {
  from, to {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}
.section-title-contanier h2 {
  color: white;
  font-family: "Rubik", sans-serif;
  font-size: clamp(1.2em, 1.2vw, 2.4em);
  font-weight: 400;
  margin: 0;
  padding-block: 40px 15px;
  text-align: center;
}
.section-title-contanier h2 > .magic {
  display: inline-block;
  position: relative;
}
.section-title-contanier h2 > .magic > .magic-star {
  --size: clamp(10px, 0.75vw, 15px);
  animation: scale 700ms ease forwards;
  display: block;
  height: var(--size);
  left: var(--star-left);
  position: absolute;
  top: var(--star-top);
  width: var(--size);
}
.section-title-contanier h2 > .magic > .magic-star > svg {
  animation: rotate 1000ms linear infinite;
  display: block;
  opacity: 0.7;
}
.section-title-contanier h2 > .magic > .magic-star > svg > path {
  fill: var(--violet);
}
.section-title-contanier h2 > .magic > .magic-text {
  animation: background-pan 3s linear infinite;
  background: linear-gradient(to right, var(--purple), var(--violet), var(--pink), var(--purple));
  background-size: 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  white-space: nowrap;
}

.days__body main .resources-section {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-auto-rows: 300px;
  gap: 20px 10px;
  padding-inline: 90px;
}
@media (max-width: 1400px) {
  .days__body main .resources-section {
    padding-inline: 40px;
  }
}
@media (max-width: 1000px) {
  .days__body main .resources-section {
    padding-inline: 20px;
  }
}
@media (max-width: 600px) {
  .days__body main .resources-section {
    padding-inline: 5px;
  }
}

.days__body main .yt-video-contanier {
  display: grid;
}
.days__body main .yt-video-contanier a {
  overflow: hidden;
}
.days__body main .yt-video-contanier a .video-thumbnail {
  border-radius: 10px;
  width: 100%;
}
.days__body main .yt-video-contanier .video-data {
  display: grid;
  grid-template-columns: 50px 1fr;
  gap: 10px;
}
.days__body main .yt-video-contanier .video-data .channel-logo img {
  border-radius: 50%;
  width: 40px;
}
.days__body main .yt-video-contanier .video-data .title-channel-name p:nth-of-type(1) {
  color: #fff;
}
.days__body main .yt-video-contanier .video-data .title-channel-name a {
  padding-block: 7px 0;
  color: rgba(255, 255, 255, 0.3294117647);
}

main .resources-section .yt-video-contanier .yt-vid-link {
  position: relative;
  overflow: hidden;
  z-index: 3;
}
main .resources-section .yt-video-contanier .yt-vid-link .thumbnail-gif {
  cursor: pointer;
  opacity: 0;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
main .resources-section .yt-video-contanier .yt-vid-link .thumbnail-gif:hover {
  opacity: 1;
}

.days__body main .resources-section .book-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
}
.days__body main .resources-section .book-container picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.days__body main .resources-section .book-container .book-details {
  display: flex;
  flex-direction: column;
  padding: 15px;
  color: rgba(255, 255, 255, 0.568627451);
  border: solid 2px;
  border-image: linear-gradient(180deg, hsla(300, 100%, 19%, 0.205), rgba(0, 0, 0, 0), hsla(300, 100%, 19%, 0.205));
  border-image-slice: 1;
  position: relative;
}
.days__body main .resources-section .book-container .book-details p:first-child {
  color: #fff;
  padding-block: 5px 15px;
}
.days__body main .resources-section .book-container .book-details p:first-child span {
  font-weight: 600;
