.countdown {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;

  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--701);
  font-family: "Poppins", sans-serif;
  overflow: hidden;

  #time {
    // border: solid 1px red; ////
    display: flex;
    gap: 30px;
    transform: scale(75%);

    .circle {
      position: relative;
      width: 150px;
      height: 150px;
      display: flex;
      justify-content: center;
      align-items: center;

      svg {
        position: relative;
        width: 150px;
        height: 150px;
        transform: rotate(270deg);
        circle {
          width: 100%;
          height: 100%;
          fill: transparent;
          stroke-width: 8;
          stroke: #282828;
          stroke-linecap: round;
          transform: translate(5px, 5px);

          &:nth-child(2) {
            stroke: var(--clr);
            stroke-dasharray: 440;
            stroke-dashoffset: 440;
          }
        }
      }
    }
    div {
      position: absolute;
      text-align: center;
      font-weight: 500;
      color: #fff;
      font-size: 1.5em;
      span {
        color: hsla(0, 0%, 100%, 0.747);

        position: absolute;
        transform: translateX(-50%) translateY(5px);
        font-size: 0.35em;
        font-weight: 300;
        letter-spacing: 0.1px;
        text-transform: uppercase;
      }
    }
    .dots {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: flex-start;
      z-index: 1000;
      &:before {
        content: "";
        position: absolute;
        top: -3px;
        width: 15px;
        height: 15px;
        background: var(--clr);
        border-radius: 50%;
        box-shadow: 0 0 20px var(--clr), 0 0 60px var(--clr);
      }
    }
  }
}
